.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.header {
  left: 0;
  right: 0;
  top: 0;
  height: 56px;
  background-color: #eaeaea;
  padding-left: 20px;
  padding-right: 20px;
}
.main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  height: 100%;
  background-color: #eaeaea;
}
.side {
  /* width: 30%; */
  min-width: 250px;
  min-height: calc(100vh - 58px);
  border-right: 1px solid #ddd;
  background-color: #eaeaea;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  position: relative;
  z-index: 2;
  -webkit-box-ordinal-group: 2;
      -ms-flex-order: 1;
          order: 1;
}
.content {
  z-index: 0;
  -webkit-box-ordinal-group: 3;
      -ms-flex-order: 2;
          order: 2;
  width: calc(100% - 252px);
  position: fixed;
  right: 0;
  height: calc(100vh - 4.14em);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #eaeaea;
}
